.skillContainer{
    text-align: center;
    padding-top: 40px;
    padding-bottom: 10px;
    margin-top: 15px;
    height: 250px;
    margin-bottom: 15px;
}
.skillImage:hover {
    animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
    transform: translate3d(0, 0, 0);
    backface-visibility: hidden;
    perspective: 1000px;
}
.skillImage{
    height: 120px;
    width: 120px;
}
.skillName{
    padding-top: 30px;
    font-size: 20px;
}
@keyframes shake {
    10%, 90% {
      transform: translate3d(-1px, 0, 0);
    }
    
    20%, 80% {
      transform: translate3d(2px, 0, 0);
    }
  
    30%, 50%, 70% {
      transform: translate3d(-4px, 0, 0);
    }
  
    40%, 60% {
      transform: translate3d(4px, 0, 0);
    }
  }